.loader {
    width: 100px;
    height: 100px;
    border-radius: 50px;
    border: 2px dashed teal;
    animation: rotate 1s infinite linear;
}

@keyframes rotate {
    from {
        transform: rotate(8deg) scale(1);
    }
    to {
        transform: rotate(360deg) scale(1.4);
    }

}